<template>
  <transition name="slide">
    <div class="user-wrap">
      <i class="iconfont icon-back icon-fanhui" @click="$router.back()"></i>
      <div class="switches-box">
        <div class="title">评论区</div>
      </div>
      <div class="song">
        <img v-lazy="song.image" alt="">
        <div class="section">
          <div class="name">{{song.name}}</div>
          <div class="singer">{{song.singer}}</div>
        </div>
      </div>
      <dl class="discussion">
        <dt>精彩评论</dt>
        <dd v-for="(data, index) in discussion" :key="index">
          <!-- <i class="avator iconfont icon-touxiang" v-if="!islogin"></i> -->
          <img class="avator" :src="data.imgUrl"/>
          <div class="area">
            <div class="name">{{data.name}}</div>
            <div class="time">{{dateFormat(data.time, 'mm-dd hh:MM')}}</div>
            <div class="content">{{data.content}}</div>
          </div>
        </dd>
        <dd class="empty" v-show="!discussion.length">暂无评论</dd>
      </dl>
      <div class="discussion-edit">
        <input class="edit" type="text" placeholder="开始你的评论" v-model="text" >
        <div class="send" @click="send">发送</div>
      </div>
    </div>
  </transition>
</template>

<script>
import {dateFormat} from '@/common/js/until'
import storage from '@/common/js/store'
import {mapGetters} from 'vuex'
let local = storage.local
  export default {
    data() {
      return {
        song: {},
        discussion: [
          // {
          //   name: '辅导员',
          //   time: +new Date(),
          //   content: '好听好听好听好听好听好听好听好听'
          // },
          // {
          //   name: '辅导员',
          //   time: +new Date(),
          //   content: '好听好听好听好听好听好听好听好听'
          // }
        ],
        text: '',
        allDiscussion: null
      }
    },
    computed: {
      ...mapGetters(['islogin'])
    },
    activated() {
      this.song = this.$route.query
      this.song.image = this.song.image.split('?')[0]
      this.allDiscussion = local.get('comment', true) || {}
      this.discussion = this.allDiscussion[this.song.id] || []
    },
    methods: {
      dateFormat,
      handle(url) {
        this.$router.push(url)
      },
      send() {
        if (!this.text) {
          this.$toast({
            content: '请输入评论内容'
          })
          return
        }
        let obj = {
          name: !this.islogin ? '游客' : '133***86516',
          time: +new Date(),
          content: this.text,
          imgUrl: this.islogin ? require('@/assets/rabit.jpg') : require('@/assets/default.png')
        }
        this.discussion.push(obj)
        this.text = ''
        this.allDiscussion[this.song.id] = this.discussion
        local.set('comment', this.allDiscussion, true)
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../common/scss/variable";
  .empty {
    margin: 0 auto;
    height: 100px;
    line-height: 100px;
    display: flex;
    justify-content: center;
    color: #999;
  }
  .discussion {
    dt {
      padding: 10px 0;
    }
    dd {
      display: flex;
      margin-bottom: 10px;
      .avator {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        font-size: 28px;
        color: #c8c5c5;
      }
      .area {
        padding-left: 10px;
        .name {
          font-size: 14px;
          color: #896456;
        }
        .time {
          font-size: 12px;
          padding: 3px 0;
          color: #999;
        }
        .content {
          font-size: 14px;
        }
      }
    }
  }
  .discussion-edit {
    display: flex;
    align-items: center;
    position: fixed;
    bottom: 10px;
    left: 20px;
    right: 20px;
    .send {
      padding-left: 10px;
      font-size: 14px;
    }
    .edit {
      flex: 1;
      display: flex;
      align-items: center;
      height: 18px;
      margin: 0 5px;
      font-size: $font-size-medium;
      color: $color-text;
      background: #f5f5f5;
      padding: 6px 12px;
      border-radius: 26px;
      &::placeholder {
        color: $color-text-l;
      }
    }
  }
  .song {
    display: flex;
    padding: 0 20px;
    img {
      width: 60px;
    }
    .section {
      padding-left: 10px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      .name {
        padding-bottom: 10px;
      }
      .singer {
        color: #666;
      }
    }
  }
  .discussion {
    padding: 20px;
  }
  .avator {
    text-align: center;
    img {
      border-radius: 50%;
    }
    .text {
      color: #666;
      margin-top: 10px;
    }
    .change {
      border: 1px solid #666;
      width: fit-content;
      padding: 4px 6px;
      border-radius: 4px;
      margin: 10px auto 0;
      font-size: 12px;
    }
  }
  .fun-list {
    padding: 50px 0;
    margin: 0 20px;
    li {
      line-height: 46px;
      height: 46px;
      font-size: 16px;
      color: #666;
      border-bottom: 1px solid #eaeaea;
      position: relative;
      &::after {
        content: '';
        display: inline-block;
        background: url('~@/assets/rightgreyrow.png');
        background-size: 100% 100%;
        width: 6px;
        height: 12px;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  .user-wrap {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: $color-background;
    z-index: 11;
    // &.slide-enter-active, &.slide-leave-active {
    //   transition: all .3s;
    // }
    // &.slide-enter, &.slide-leave-to {
    //   transform: translate3d(100%, 0, 0);
    // }
    .title {
      height: 45px;
      line-height: 45px;
      text-align: center;
    }
    .icon-back {
      position: absolute;
      top: 0;
      left: 6px;
      padding: 10px;
      font-size: $font-size-large;
      color: $color-theme;
    }
    .switches-box {
      margin: 0 0 30px;
    }
    .play-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 135px;
      margin: 0 auto;
      padding: 7px 0;
      color: $color-text-l;
      border: 1px solid $color-text-l;
      border-radius: 100px;
      box-sizing: border-box;
      .icon-play {
        margin-right: 6px;
        font-size: $font-size-medium-x;
      }
      .text {
        font-size: $font-size-small;
      }
    }
    .list-box {
      position: absolute;
      left: 0;
      top: 110px;
      right: 0;
      bottom: 0;
      .list-scroll {
        height: 100%;
        overflow: hidden;
        .list-inner {
          padding: 20px 30px;
        }
      }
    }
    .no-result-box {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
</style>



